import React from "react"
import { Col, Container, Jumbotron, Row } from "react-bootstrap"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Lorem from "../components/home/lorem"
import ShowCase from "../components/home/showCase"
import Img from "gatsby-image"
import Footer from "../components/footer"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faArrowRight } from "@fortawesome/free-solid-svg-icons"
import Parallax from "../components/motion/parallax"
import Heading from "../components/motion/heading"

const IndexPage = ({ data }) => {
  const showCaseData = {
    contents: data.site.siteMetadata.latestProjects,
    images: [
      {
        alt: "Books, accounting application",
        fluid: data.project1.childImageSharp.fluid,
      },
      {
        alt: "QRalacarte.com",
        fluid: data.project2.childImageSharp.fluid,
      },
    ],
  }

  console.log(showCaseData)

  return (
    <Layout>
      <SEO title="Home" />
      <Parallax>
        <Jumbotron className="bg-transparent" style={{ minHeight: "70vh" }}>
          <Container>
            <Row>
              <Col md="9">
                <p className="lead">
                  I am a technology enthusiast, and experienced developer. I'm
                  currently working in the front-end, but I've also experience
                  in several full-stack technologies. During my career I built
                  different softwares including desktop and mobile applications,
                  for businesses, individuals and for personal use. If you'd
                  like to get in contact please feel free to drop me an email
                  and I'll get back to you as soon as possible.
                </p>
                <p>
                  <a
                    href="mailto:nicola.danie@gmail.com"
                    className="btn btn-link"
                  >
                    Get in touch <FontAwesomeIcon icon={faArrowRight} />
                  </a>
                </p>
              </Col>
            </Row>
          </Container>
        </Jumbotron>
      </Parallax>

      <ShowCase data={showCaseData} />

      <Lorem image={data.project3.childImageSharp.fluid} />

      {/** About */}
      <Parallax>
        <Jumbotron
          fluid
          id="about"
          className="bg-transparent mb-0"
          style={{ minHeight: "100vh" }}
        >
          <Container fluid>
            <Row className="justify-content-around">
              <Col lg="4">
                <Img
                  className="img-fluid"
                  alt="Profile of Nicola D'Aniello"
                  fluid={data.profile.childImageSharp.fluid}
                />
              </Col>
              <Col lg="5">
                <Heading>About</Heading>
                <p className="lead">
                  I’m a professional front-end developer and freelance software
                  developer. After self-teaching coding at age twelve, I
                  developed a strong passion for this industry, and completed a
                  BSc (computing) degree, which encouraged me to go deep into
                  various subjects of computer science and gain skills in
                  several modern full-stack technologies.
                  <br />
                  If you'd like to hire me or you are intrested in my CV please
                  drop me an email, and I'll get back to you as soon as
                  possible.
                </p>
                <p>
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href="mailto:nicola.danie@gmail.com"
                    className="btn btn-link"
                  >
                    Get in touch <FontAwesomeIcon icon={faArrowRight} />
                  </a>
                </p>
              </Col>
            </Row>
          </Container>
        </Jumbotron>
      </Parallax>

      <Footer />
    </Layout>
  )
}

export default IndexPage

export const fluidImage = graphql`
  fragment fluidImage on File {
    childImageSharp {
      fluid(maxWidth: 1000) {
        ...GatsbyImageSharpFluid
      }
    }
  }
`

export const pageQuery = graphql`
  query {
    project1: file(relativePath: { eq: "books.png" }) {
      ...fluidImage
    }
    project2: file(relativePath: { eq: "qralacarte.png" }) {
      ...fluidImage
    }
    project3: file(relativePath: { eq: "image.jpg" }) {
      ...fluidImage
    }
    profile: file(relativePath: { eq: "profile.jpg" }) {
      ...fluidImage
    }
    site {
      siteMetadata {
        latestProjects {
          title
          copy
          cta
          link
        }
      }
    }
  }
`
